import '../globals.css';
import Sidebar from '@/components/Sidebar';
import { cookies } from 'next/headers';
import { getTenant } from '@/lib/tenants';

export const metadata = { title: 'IIoT Demo Dashboard', description: 'Next.js 16 + SSE' };

export default function ShellLayout({ children }: { children: React.ReactNode }) {
  const c = cookies();
  const tenantId = c.get('tenant')?.value || null;
  const role = c.get('role')?.value || 'user';
  const t = getTenant(tenantId || undefined);
  return (
    <html lang="en">
      <body>
        <div className="shell">
          <aside><Sidebar/></aside>
          <section className="content">
            <div style={{opacity:.8, fontSize:12, marginBottom:8}}>
              Tenant: <strong>{t?.name ?? '—'}</strong> • Role: <strong style={{textTransform:'capitalize'}}>{role}</strong>
            </div>
            {children}
          </section>
        </div>
        <footer>© {new Date().getFullYear()} ARK IIoT Demo</footer>
      </body>
    </html>
  );
}
